import React from 'react';

let evaluations = ['20120',
 '',
 '',
 '',
 '']

class EvaluationComponent extends React.Component{
    render(){
        let eva = [];
        evaluations.forEach((val,index)=>{
            eva.push(<li key={index}>{val}</li>)
        })
        return(
            <ul>
                {eva}
            </ul>
        )
    }
}

export default EvaluationComponent;


