import React from 'react';

let skills =                ['985',
							'PCH5web3D',
							'WEBes6',
							'JavaScriptjQueryzeptoAngularVUEReactjQuery,vue',
							'JavaScriptJS',
							'Ajax,RESTful',
							'JSJS',
							'MVCMVPMVVM;',
							'gruntgulp,webpack',
							'nodejskoamongodbnpm;',
							'githubmacmacmac;',
							'svngit;'
]

class SkillsListComponent extends React.Component{
    render(){
        let skillsItem = [];
        skills.forEach((val,index)=>{
            skillsItem.push(<li key={index}>{val}</li>)
        })
        return(
            <ul>
                {skillsItem}
            </ul>
        )
    }
}

export default SkillsListComponent;


