require('styles/Projects.scss');

import React from 'react';

let jobs = [{
  time: '201512  201611',
  company: '',
  projects: [{
    'name': '',
    'link': 'http://www.damor.cn',
    'descript': '',
    'duty': 'WEBUI',
    'technology': 'gulp,requireJSjs',
  }]
}, {
  time: '20171  ',
  company: '',
  projects: [{
    name: 'domeos',
    link: 'https://github.com/domeos',
    descript: 'DockerDomeOS,',
    duty: ',,,bug',
    technology: 'angular,,select,form,gulp'
  },{
    name: 'app',
    descript:'',
    duty: 'H5,,',
    technology: `
    vue,elementUI,elementtableH5nodejs,koa-grace,thinkjs,thinkjsproxy,,mongodb
    native,,native
    gulp+webpack,gulp,webpackjs`
  },{
    name: 'compass',
    descript:',,,,,',
    duty: 'WEBUI',
    technology:`vue,iview,echartsiviewtable,vue-cli,webkack`
  }]
}];

function _t(key) {
  let map = {
    name:':',
    time: ':',
    company: ':',
    project: ':',
    link: ':',
    descript: ':',
    duty: ':',
    technology: ':'
  };
  return map[key]
}

class JobItem extends React.Component {
  render() {
    let list = (function (jobInfo) {
      let res = [], index = 0;
      for (let key in jobInfo) {
        index++
        let title,value;
        if(key === 'projects'){
          value = [];
          jobInfo.projects.forEach((project,index)=>{
            let res = [];
            for(let projectkey in project){
              let distClass = 'dist';
              if(projectkey == 'name'){
                distClass = 'dist project-title';
              }
              res.push(
                <div className="row">
                  <div className="title">{_t(projectkey)}</div>
                  <div className={distClass}>{project[projectkey]}</div>
                </div>
              )
            }
            value.push(
              <div className="project">
                {res}
              </div>
            );
          })
        } else{
          title = _t(key);
          value = jobInfo[key];
        }
        res.push(
          <div className="row" key={index}>
            <div className="title">
              {title}
            </div>
            <div className="dist">
              {value}
            </div>
          </div>
        )
      }
      return res;
    })(this.props.info)
    return (
      <section className="job">
        {list}
      </section>
    )
  }
}

class JobsComponent extends React.Component {
  render() {
    let list = [];
    jobs.forEach((val, index) => {
      list.push(<JobItem info={val} key={index} />)
    })
    return (
      <div className="content">
        {list}
      </div>
    )
  }
}

export default JobsComponent;


