require('normalize.css/normalize.css');
require('styles/App.scss');
require('styles/Response.scss');
import React from 'react';
import UserImage from './userimage';
import SkillsBar from './skillsbar';
import UserInfo from './userinfo';
import Title from './title';
import SkillsList from './skillslist';
import Projects from './projects';
import Jobs from './jobs';
import Certificate from './certificate';
import Evaluation from './evaluation';
import Canvas from './canvas'
import QueueAnim from 'rc-queue-anim';
import TweenOne from 'rc-tween-one';
let yeomanImage = require('../images/yeoman.png');
let TweenOneGroup = TweenOne.TweenOneGroup;
let titleImages = ['Image 1.png', 'Image 2.png', 'Image 3.png', 'Image 4.png', 'Image 5.png']

titleImages = (function (images) {
  let imgs = [];
  images.forEach(function (val, index) {
    let a = require('../images/' + val);
    imgs.push(a);
  })
  return imgs;
})(titleImages)


class AppComponent extends React.Component {

  render() {
    return (
      <div className="root">
        <Canvas />
        <TweenOneGroup enter={{ y: 4000, opacity: 0, type: 'from', duration: 3000, ease: 'easeOutBack', delay: 2000 }} >
          <div className="container" key="container">
            <div className="left-bar">
              <UserImage />
              <UserInfo />
              <SkillsBar />
            </div>
            <div className="right-bar">
              <header>
                <h1></h1>
                <p></p>
              </header>

              <div className="education" key="key1">
                <Title info={{ 'image': titleImages[0], 'text': '', 'alt': '' }} />
                <section className='content'>
                  <p>20119  20157       </p>
                  <p>
                    
								</p>
                  <p>15%</p>
                </section>
              </div>

              <div className="skills" key="key2">
                <Title info={{ 'image': titleImages[1], 'text': 'IT', 'alt': 'IT' }} />
                <div className="content">
                  <SkillsList />
                </div>
              </div>

              <div className="experience" key="key3">
                <Title info={{ 'image': titleImages[2], 'text': '', 'alt': '' }} />
                <div className="content">
                  <div className="projects">
                    <h2></h2>
                    <Projects />
                  </div>
                  <div className="jobs">
                    <h2></h2>
                    <Jobs />
                  </div>
                </div>
              </div>
              {/*
              <div className="certificate" key="key4">
                <Title info={{ 'image': titleImages[4], 'text': '', 'alt': '' }} />
                <div className="content">
                  <Certificate />
                </div>
              </div>
              */}

              <div className="evaluation" key="key5">
                <Title info={{ 'image': titleImages[3], 'text': '', 'alt': '' }} />
                <div className="content">
                  <Evaluation />
                </div>
              </div>

            </div>
          </div>
        </TweenOneGroup>
      </div>
    );
  }
}

AppComponent.defaultProps = {
};

export default AppComponent;



