import React from 'react';

let certificates = ['','','CET4','']

class CertificateComponent extends React.Component{
    render(){
        let certis = [];
        certificates.forEach((val,index)=>{
            certis.push(<li key={index}>{val}</li>)
        })
        return(
            <ul>
                {certis}
            </ul>
        )
    }
}

export default CertificateComponent;


